import { LitElement } from "lit-element";
// import { directive } from 'lit-html'
import { html } from "lit-element";
import { classMap } from "lit-html/directives/class-map";
import uuid from "uuid";
import "./chordGridInput/ChordGridInput";
import "./CacheInput";
import { ToolBox } from "./ToolBox";
import { ChordGrid } from "./chordGrid/ChordGrid";
import {
  ClearIcon,
  CopyIcon,
  InsertGridIcon,
  PasteIcon,
  VolumeUpIcon, VolumeOffIcon,
  DotIcon, MarkXIcon, RectangleIcon, TriangleIcon
} from "../components/Icons.js";
import { SectionName } from "../components/SectionName";

import { playSymbols } from "../sounds/player";

const chromaticScale1 = [
  "C",
  "Db",
  "D",
  "Eb",
  "E",
  "F",
  "Gb",
  "G",
  "Ab",
  "A",
  "Bb",
  "B",
];
const chromaticScale2 = [
  "C",
  "C#",
  "D",
  "D#",
  "E",
  "F",
  "F#",
  "G",
  "G#",
  "A",
  "A#",
  "B",
];

const getNextNote = (note, semitones) => {
  const isNoteFlat = chromaticScale1.includes(note);
  // console.error({ isNoteFlat });

  const index = isNoteFlat
    ? chromaticScale1.findIndex((it) => it === note)
    : chromaticScale2.findIndex((it) => it === note);

  if (index === -1) {
    return "";
  }

  // console.error({ isNoteFlat, index });

  const transposedNote = isNoteFlat
    ? chromaticScale1[(index + 12 + semitones) % 12]
    : chromaticScale2[(index + 12 + semitones) % 12];

  // console.error(transposedNote);
  return transposedNote;
};

window.getNextNote = getNextNote;

const transposeFrets = (fretsString, semitones) => {
  const fretsArray = fretsString.split(".").map((it) => it.split("/"));
  // console.log(fretsArray);

  const transposedFrets = fretsArray.map((it) => {
    // console.log('before', it);
    const inner = it.map((it) => {
      // console.log('inner:before', it);
      return parseInt(it, 10) + semitones;
    });
    // console.log('inner',inner);
    return inner;
  });

  // console.log('transposedFrets',transposedFrets);

  /*** check if the lowest fret is less than 0 */
  const isTooLow = transposedFrets
    .reduce((a, c) => a.concat(c), [])
    .filter(Number)
    .some((it) => it < 0);

  // console.log({ isTooLow });

  if (isTooLow) {
    return false;
  }

  const adjustedFrets = isTooLow
    ? transposedFrets.map((it) =>
        // it.map((i) => (!Number(i) ? i : i + 12))
        it.map((i) => (isNaN(i) ? i : i + 12))
      )
    : transposedFrets;

  // console.log('adjustedFrets', adjustedFrets);

  const newFretsString = adjustedFrets
    // .map(it => (it.map(i => Number(i) ? i : 'x')).join('/'))
    .map((it) => it.map((i) => (isNaN(i) ? "x" : i)).join("/"))
    .join(".");

  // console.log('newFretsString', newFretsString)

  return newFretsString;
};

const getNewChord = (chordString) => {
  return {
    id: uuid.v4(),
    chordString,
    section: "",
    ending: 0,
    repeatStart: false,
    repeatEnd: false,
  };
};

class SheetEditor extends LitElement {
  createRenderRoot() {
    return this;
  }
  constructor() {
    super();
    this.songInfo = {
      name: "",
      description: "",
    };
    this.chords = [];
    this.chord = {
      chordString: "3.x.3.3.3.x@Gm7",
      id: "",
    };
    this.multipleSelection = [];

    this.clipboardChords = [];

    this.showCacheInput = false;
    this.visiblePanelIndex = 1;
  }

  static get properties() {
    return {
      chords: { attribute: false },
      chord: { attribute: false },
      songInfo: { attribute: false },
      multipleSelection: { attribute: false },
      isAlternateOrder: { attribute: false },
      chordCache: { attribute: false },
      showCacheInput: { attribute: false },
      visiblePanelIndex: { attribute: false },
      soundsEnabled: { attribute: false },
    };
  }

  scrollToLastGrid() {
    if (this.chords.length === 0) return;
    const lastGridId = this.chords[this.chords.length - 1].id;
    // console.log(lastGridId);
    const element = document.querySelector(`#chord-grid-${lastGridId}`);
    // console.log("element: ", element);
    if (element) {
      element.scrollIntoView({
        block: "end",
        inline: "nearest",
        behavior: "smooth",
      });
    }
  }

  dispatchSetChords(chords) {
    this.dispatchEvent(new CustomEvent("set-chords", { detail: chords }));
  }

  handleAddSongInfo(e) {
    // console.log(e.target.value, e.target.name);
    const name = e.target.name;
    const value = e.target.value;
    this.dispatchEvent(
      new CustomEvent("set-song-info", {
        detail: { [name]: value },
      })
    );
  }

  handleSelectGrid(id, e) {
    // console.log(e);
    e.stopPropagation();

    // console.log("handleSelectGrid()", id);

    // multiple chord selection, shift+click
    if (e.shiftKey) {
      this.chord = {
        chordString: "3.x.3.3.3.x@Gm7",
        id: "",
      };

      const alreadySelected = this.multipleSelection.find((it) => it.id === id);

      if (alreadySelected) {
        this.multipleSelection = this.multipleSelection.filter(
          (it) => it.id !== id
        );

        return;
      }

      // not selected before, add to multiple selection
      // const selectedChord = this.state.chords.find((chord) => chord.id === id);
      const selectedChord = this.chords.find((chord) => chord.id === id);
      this.multipleSelection = this.multipleSelection.concat(selectedChord);
      return;
    }
    // single selection mode: click; if selected unselect
    if (id === this.chord.id) {
      this.chord = { chordString: "3.x.3.3.3.x@Gm7", id: "" };
      return;
    }
    //move chord to editor in single selection mode
    const selectedChord = this.chords.find((chord) => chord.id === id);
    this.chord = selectedChord;
    this.multipleSelection = [];
    // const notes = getChordNotes(selectedChord.chordString);
    // playNote(notes);
    if(this.soundsEnabled){
      playSymbols(selectedChord.chordString)
    }
  }

  handleDeselectGrid() {
    if (this.chord.id || this.multipleSelection.length > 0) {
      // console.log('deselect grid', this.chord.id, ev.target);
      this.chord = {
        chordString: "3.x.3.3.3.x@Gm7",
        id: "",
      };
      this.multipleSelection = [];
    }
  }

  handleAddChord(ev) {
    // console.log(ev.detail);
    const chordString = ev.detail;
    //append mode
    if (!this.chord.id) {
      const newChord = getNewChord(chordString);
      const chords = this.chords.concat(newChord);
      this.dispatchSetChords(chords);
      setTimeout(() => this.scrollToLastGrid(), 300);
      return;
    }
    // edit mode
    if (this.chord.id) {
      const selectedChord = this.chords.find(
        (chord) => chord.id === this.chord.id
      );
      const index = this.chords.findIndex(
        (chord) => chord.id === this.chord.id
      );

      const chords = this.chords
        .slice(0, index)
        .concat(
          { ...selectedChord, chordString },
          this.chords.slice(index + 1)
        );
      this.dispatchSetChords(chords);
      // adding from cache load to editor
      return;
    }
  }

  handleAddCacheChord(ev){
    
    const chordString = ev.detail;
    
    if (!this.chord.id) {
      const newChord = getNewChord(chordString);
      const chords = this.chords.concat(newChord);
      this.dispatchSetChords(chords);
      setTimeout(() => this.scrollToLastGrid(), 300);
      return;
    }

    if (this.chord.id) {
      const selectedChord = this.chords.find(
        (chord) => chord.id === this.chord.id
      );
      const index = this.chords.findIndex(
        (chord) => chord.id === this.chord.id
      );

      const chords = this.chords
        .slice(0, index)
        .concat(
          { ...selectedChord, chordString},
          this.chords.slice(index + 1)
        );
      this.dispatchSetChords(chords);
      this.chord = { ...selectedChord, chordString }
      return;
    }

  }

  handleRemove() {
    if (!this.chord.id) {
      return;
    }
    //console.log('handleRemove')
    const chords = this.chords.filter((chord) => chord.id !== this.chord.id);
    // this.dispatchEvent(new CustomEvent('set-chords', {detail: chords }));
    this.dispatchSetChords(chords);
    this.chord = { chordString: "3.x.3.3.3.x@Gm7", id: "" };
  }

  handleInsert() {
    // no chord is selected
    if (!this.chord.id) {
      return;
    }
    // there is a selected chord
    const index = this.chords.findIndex((chord) => chord.id === this.chord.id);

    const newChord = getNewChord("x.x.x.x.x.x@");

    const chords = this.chords
      .slice(0, index)
      .concat(newChord, this.chords.slice(index));

    // this.dispatchEvent(new CustomEvent('set-chords', {detail: chords}));
    this.dispatchSetChords(chords);
    this.chord = newChord;

    if(document.querySelector("#chord-name-input")){
      document.querySelector("#chord-name-input").focus();
    }
  }

  handleCopy() {
    // copy chords in multiple selection if there are any
    if (this.multipleSelection.length !== 0) {
      const chordStrings = this.multipleSelection.map((it) => it.chordString);
      this.clipboardChords = chordStrings;
      this.multipleSelection = [];
      // console.log(this.clipboardChords);
      return;
    }
    // if no chord is selected in single selection mode
    if (!this.chord.id) return;

    // a single chord is selected, copy that
    this.clipboardChords = this.clipboardChords.concat(this.chord.chordString);
    this.multipleSelection = [];
    this.chord = {
      chordString: "3.x.3.3.3.x@Gm7",
      id: "",
    };

    // console.log(this.clipboardChords);
  }

  handlePaste() {
    if (this.clipboardChords.length === 0) return;

    const newChords = this.clipboardChords.map((chordString) => {
      return getNewChord(chordString);
    });

    // add copied chords and scrool to last chord
    const chords = this.chords.concat(newChords);
    // this.dispatchEvent(new CustomEvent('set-chords', { detail: chords }));
    this.dispatchSetChords(chords);
    this.clipboardChords = [];
    setTimeout(() => this.scrollToLastGrid(), 300);
  }

  handleAddToolBoxItem(item, detail, e) {
    // console.log(item, detail);
    // no chord is selected
    if (!this.chord.id) {
      return;
    }

    if (item === "section") {
      const chords = this.chords.map((chord) =>
        chord.id === this.chord.id
          ? Object.assign({}, chord, {
              section: chord.section === detail ? "" : detail,
            })
          : chord
      );

      this.dispatchSetChords(chords);
    } else if (item === "ending") {
      const chords = this.chords.map((chord) => {
        return chord.id === this.chord.id
          ? Object.assign({}, chord, {
              ending: chord.ending === detail ? 0 : detail,
            })
          : chord;
      });
      this.dispatchSetChords(chords);
    } else {
      // repeat start, repeat end
      const chords = this.chords.map((chord) =>
        chord.id === this.chord.id
          ? Object.assign({}, chord, { [item]: !chord[item] })
          : chord
      );
      this.dispatchSetChords(chords);
    }
  }

  extractChordParts(chordName) {
    const match = chordName.match(/^[A-G#b]{1,2}/);
    if (!match) return ["", "", ""];
    const chordRoot = match[0];
    const rest = chordName.replace(/^[A-G#b]{1,2}/, "");
    const [chordQuality, slashNote = ""] = rest.split("/");
    //console.log(chordRoot);
    return [chordRoot, chordQuality, slashNote];
    // return chordRoot.length === 1 ? chordRoot.toUpperCase() + chordQuality :
    //   chordRoot.substr(0, 1).toUpperCase().concat(chordRoot.substr(1, 1)) + chordQuality
  }

  handleTranspose(semitones) {
    // console.log({ semitones });
    // console.log(this.chords);
    let canTrasnpose = true;

    const transposedChords = this.chords.map((chord) => {
      // console.log(chord);
      const [frets, chordName] = chord.chordString.split("@");
      const [chordRoot, chordQuality, slashNote] = this.extractChordParts(
        chordName
      );
      // const [chordQuality, slashNote = ""] = rest.split("/");
      // console.log({ chordRoot, chordQuality, slashNote });

      const transposedNote = getNextNote(chordRoot, semitones);
      const transposedSlashNote = slashNote
        ? `/${getNextNote(slashNote, semitones)}`
        : "";
      // console.log({ transposedNote, transposedSlashNote });

      const transposedName = `${transposedNote}${chordQuality}${transposedSlashNote}`;
      // console.log(transposedName);

      // console.log(frets);

      const newFretsString = transposeFrets(frets, semitones);
      // console.log(`${newFretsString}@${transposedName}`);
      if (!newFretsString) {
        canTrasnpose = false;
        return chord;
      }
      return {
        ...chord,
        chordString: `${newFretsString}@${transposedName}`,
      };
    });

    // console.log(transposedChords);
    if (canTrasnpose) {
      this.dispatchSetChords(transposedChords);
    } else {
      window.alert("Some of the grids can't be transposed any lower...");
    }
  }

  handleSwapInputs() {
    this.showCacheInput = !this.showCacheInput;
  }

  handleVisiblePanel(index) {
    this.visiblePanelIndex = index;
  }

  handleMute(){
    this.dispatchEvent(new CustomEvent('toggle-sound', {bubbles: true}))
  }

  render() {
    // console.log("SheetEditor.render()", this.chord.chordString);
    return html`
      <div class="container-fluid fadeIn">
        <div class="row">
          <div class="col-md-4 col-lg-3 px-2 text-center">
            <div class="btn-group w-100 mt-2" role="group">
              <button
                title=${this.soundsEnabled ? 'Sound is on' : 'Sound is off'}
                class="btn btn-primary ripple"
                @click=${this.handleMute}
              >
                ${this.soundsEnabled ? VolumeUpIcon() : VolumeOffIcon()}
              </button>
              <button
                title="Remove selected grid"
                class="btn btn-primary btn ripple"
                @click=${this.handleRemove}
              >
                ${ClearIcon()}
              </button>
              <button
                title="Insert before selected grid"
                class="btn btn-primary btn ripple"
                @click=${this.handleInsert}
              >
                ${InsertGridIcon()}
              </button>
              <button
                title="Copy selected grid"
                class="btn btn-primary btn ripple"
                @click=${this.handleCopy}
              >
                ${CopyIcon()}
              </button>
              <button
                title="Paste"
                class="btn btn-primary btn ripple"
                @click=${this.handlePaste}
              >
                ${PasteIcon()}
              </button>
            </div>
            <div class="card mt-1 border rounded">
              <div class="btn-group w-100 mt-0" role="group">
                <button
                  @click=${() => this.handleVisiblePanel(0)}
                  class="btn btn-outline-primary ${classMap({'active': this.visiblePanelIndex === 0})}"
                >
                  Cache
                </button>
                <button
                  @click=${() => this.handleVisiblePanel(1)}
                  class="btn btn-outline-primary ${classMap({'active': this.visiblePanelIndex === 1})}"
                >
                  Manual
                </button>
                <button
                  @click=${() => this.handleVisiblePanel(2)}
                  class="btn btn-outline-primary shadow-none ${classMap({'active': this.visiblePanelIndex === 2})}"
                >
                  Tools
                </button>
              </div>
              <div style="height:65vh;" class="card-body px-1 subtle-dots">
                ${this.visiblePanelIndex === 0
                  ? html`
                      <div class="fadeIn">
                        <cache-input
                          .soundsEnabled=${this.soundsEnabled}
                          @add-cache-chord=${this.handleAddCacheChord}
                          .chordCache=${this.chordCache}
                        ></cache-input>
                      </div>
                    `
                  : ""}
                ${this.visiblePanelIndex === 1
                  ? html`
                    <div class="fadeIn">
                      <chord-grid-input
                        .soundsEnabled=${this.soundsEnabled}
                        .isAlternateOrder=${this.isAlternateOrder}
                        @add-chord=${this.handleAddChord}
                        .chordString=${this.chord.chordString}
                        .highlight=${this.chord.id !== ""}
                      >
                      </chord-grid-input>
                      <div>
                        ${this.isAlternateOrder
                          ? html`${DotIcon()} ${MarkXIcon()} ${TriangleIcon()}
                            ${RectangleIcon()}`
                          : html`${DotIcon()} ${MarkXIcon()} ${RectangleIcon()}
                            ${TriangleIcon()}`}
                      </div>
                    </div>
                    `
                  : ""}
                ${this.visiblePanelIndex === 2
                  ? html`
                      <div class="fadeIn">
                        ${ToolBox({
                          songInfo: this.songInfo,
                          onAddSongInfo: this.handleAddSongInfo.bind(this),
                          onAddToolBoxItem: this.handleAddToolBoxItem.bind(this),
                          onTranspose: this.handleTranspose.bind(this),
                        })}
                      </div>
                    `
                  : ""}
              </div>
            </div>
          </div>
          <div class="col-md-8 col-lg-9">
            <div
              @click=${this.handleDeselectGrid}
              class="sheet-editor-grids-container mt-1"
              style="height: 40em;overflow: auto;"
            >
              ${this.chords.map((ch, index) => {
                const { id, section, ...other } = ch;
                const selected =
                  this.chord.id === id ||
                  this.multipleSelection.find((it) => it.id === id);
                return html`
                  <div
                    id="chord-grid-${id}"
                    class="mt-2 py-2 float-left pulse ${classMap({
                      "chord-selected": selected,
                    })}"
                    @click=${this.handleSelectGrid.bind(this, id)}
                  >
                    <div style="height: 1.2em">
                      ${section ? SectionName(section) : ""}
                    </div>
                    <div class="mt-2">
                      ${ChordGrid({
                        ...other,
                        isAlternateOrder: this.isAlternateOrder,
                      })}
                    </div>
                  </div>
                `;
              })}
            </div>
          </div>
        </div>
      </div>
    `;
  }
}

customElements.define("sheet-editor", SheetEditor);
