import { html, LitElement } from "lit-element";
import { ChordGrid } from "./chordGrid/ChordGrid.js";
import { playSymbols } from "../sounds/player";

class CacheInput extends LitElement {
  createRenderRoot() {
    return this;
  }

  constructor() {
    super();
    this.searchString = ".*";
  }

  static get properties() {
    return {
      chordCache: { attribute: false },
      searchString: { attribute: false },
      soundsEnabled: { attribute: false },
    };
  }

  firstUpdated() {
    this.querySelector("#search-input").focus();
  }

  handleAddChord(ev, chordString) {
    if (ev.shiftKey) {
      // console.log('shift click');
      this.dispatchEvent(
        new CustomEvent("remove-cached-chord", { 
          detail: chordString, bubbles: true 
        }));
      return;
    }

    this.dispatchEvent(new CustomEvent("add-cache-chord", { detail: chordString }));
    // const notes = getChordNotes(chordString);
    // console.log(chord, notes);
    // playNote(notes);
    if(this.soundsEnabled){
      playSymbols(chordString);
    }
  }

  searchChord(ev) {
    // console.log(ev.target.value);
    this.searchString = ev.target.value || ".*";
  }

  render() {
    // console.log("CacheInput.render()", this.chordCache);
    // console.log(this.searchString);
    const regex = RegExp(this.searchString, "i");
    // console.log(regex);
    const filtered = this.chordCache.filter((chordString) => {
      // console.log(regex.test(chordString));
      return regex.test(chordString);
    });
    // console.log(filtered);
    return html`
      <input
        id="search-input"
        autocomplete="off"
        @input=${this.searchChord}
        class="form-control w-75 mx-auto mb-2 shadow-sm text-center"
        type="text"
        placeholder="search chord..."
      />
      <div class="cache-input">
        ${this.chordCache.length === 0 
          ? html`
              <div>
                Manually entered chords will be collected here.
              </div>
                ` : ''}
        ${filtered.map(
          (chordString) => html`
            <div
              class="d-inline-block"
              @click=${(ev) => this.handleAddChord(ev,chordString)}
            >
              ${ChordGrid({ chordString })}
            </div>
          `
        )}
      </div>
    `;
  }
}

customElements.define("cache-input", CacheInput);
