//ChordGrid
import { html } from "lit-element";
import { EmptyGrid } from "./EmptyGrid";
import { ChordName } from "./ChordName";
import { PositionNumber } from "./PositionNumber";
import { MutedText } from "./MutedText";
import { MarkX } from "./MarkX";
import { Rectangle } from "./Rectangle";
import { Dot } from "./Dot";
import { Nut } from "./Nut";
import { OpenStrings } from "./OpenStrings";
import { Volta } from "./Volta";
import { RepeatStart, RepeatEnd, EndingBar, BarLine } from "./BarLines";
import { Triangle } from './Triangle.js';

const filterNumbers = (arr) => {
  return arr.filter((it) => Number(it));
};

const processChordString = (chord) => {
  const [frets, chordName] = chord.split("@");
  const fretsArray = frets
    .split(".")
    .map((it) => (it.indexOf("/") !== -1 ? it.split("/") : it));
  //console.log("fretsArray", fretsArray);
  //console.log('fretnumbers', fretNumbers);
  const flattenedFretNumbers = filterNumbers(
    fretsArray.reduce((a, b) => a.concat(b), [])
  );
  //console.log('flattenedFretNumbers', flattenedFretNumbers);
  const maxFret =
    fretsArray.length === 0 ? 0 : Math.max(...flattenedFretNumbers);
  //console.log("maxFret", maxFret);
  // minFret is also position number
  const minFret =
    fretsArray.length === 0 ? 0 : Math.min(...flattenedFretNumbers);
  //console.log("minFret", minFret)
  const droppedFrets =
    maxFret > 6
      ? fretsArray.map((it) => {
          if (Array.isArray(it)) {
            return it.map((i) => (parseInt(i, 10) ? i - minFret + 1 : i));
          }
          return parseInt(it, 10) ? it - minFret + 1 : it;
        })
      : fretsArray;

  //console.log('droppedFrets', droppedFrets);
  const flattenedDropFrets = droppedFrets.reduce((a, b) => a.concat(b), []);
  //const minFretOfDropped = Math.min( ...filterNumbers(droppedFrets) ) //.filter( it => !isNaN( parseInt(it,10) )));
  const minFretOfDropped =
    filterNumbers(flattenedDropFrets).length === 0
      ? 0
      : Math.min(...filterNumbers(flattenedDropFrets));
  return [droppedFrets, chordName, String(minFret), minFretOfDropped, maxFret];
};

export const ChordGrid = ({ 
  chordString, ending, repeatStart, repeatEnd, endingBar, barLine, isAlternateOrder=false }) => {
  const [frets, name, position, droppedMin, max] = processChordString(
    chordString
  );
  // console.log(frets, name, position, droppedMin, max);
  //console.log(frets);
  const gridContents = frets.map((fret, index) => {
    const x = 32 + index * 16;
    const y = Array.isArray(fret)
      ? (fret[0] - 1) * 16 + 44
      : (fret - 1) * 16 + 44;

    if (Array.isArray(fret)) {
      // console.log(fret);
      // console.log(Number(fret[0]));
      // console.log(Number(fret[1]));
      // console.log(Number(fret[2]));
      if(!isAlternateOrder){
        return html`
          ${(fret[0] !== "" && !isNaN(Number(fret[0]))) ? 
            Dot({x,y, isSmall: fret[0] === fret[3]}) : ''}
          ${(fret[1] !== "" && !isNaN(Number(fret[1]))) ? 
            MarkX({
              x, y:(fret[1] - 1) * 16 + 44, 
              isLight: fret[0] === fret[1],
              hasTriangle: fret[3] === fret[1] }) : ''}
          ${(fret[2] !== "" && !isNaN(Number(fret[2]))) ? 
            Rectangle({
              x, y:(fret[2] - 1) * 16 + 44, 
                      // has dot            // has X               // has triangle
              isBig: fret[0] === fret[2] || fret[1] === fret[2] || fret[3] === fret[2]
            }) : ''}
          ${(fret[3] !== "" && !isNaN(Number(fret[3]))) ? 
            Triangle({
              x, y:(fret[3] - 1) * 16 + 44, 
              isBig: fret[0] === fret[3] || fret[1] === fret[3],
              }) : ''}
      `;
      } else {
        return html`
          ${(fret[0] !== "" && !isNaN(Number(fret[0]))) ? 
            Dot({x,y, isSmall: fret[0] === fret[2]}) : ''}
          ${(fret[1] !== "" && !isNaN(Number(fret[1]))) ? 
            MarkX({
              x, y:(fret[1] - 1) * 16 + 44, 
              isLight: fret[0] === fret[1],
              hasTriangle: fret[2] === fret[1]
            }) : ''}
          ${(fret[2] !== "" && !isNaN(Number(fret[2]))) ? 
            Triangle({
              x, y:(fret[2] - 1) * 16 + 44, 
              isBig: fret[0] === fret[2] || fret[1] === fret[2]
            }) : ''}
          ${(fret[3] !== "" && !isNaN(Number(fret[3]))) ? 
            Rectangle({
              x, y:(fret[3] - 1) * 16 + 44, 
                      // has dot              // has X              // has triangle
              isBig: fret[0] === fret[3] || fret[1] === fret[3] || fret[2] === fret[3]
            }) : ''}
      `;
      }
    } else if (isNaN(fret) || fret === "") {
      //it is muted
      //console.log('isMudet', fret);
      return html`${MutedText({x})}`
    } else {
      return parseInt(fret, 10) !== 0 ? (
        html`${Dot({x, y})}`
      ) : (
        html`${OpenStrings({x})}`
      );
    }
  });
  // console.log(gridContents)
  return html`
    <svg
      height="134"
      version="1.1"
      width="138"
      xmlns="http://www.w3.org/2000/svg"
      style="overflow: hidden;position: relative;left: -0.5px;user-select: none"
    >

    ${Number(ending) ? Volta({ending}) : ''}
    ${repeatStart ? RepeatStart() : ''}
    ${repeatEnd ? RepeatEnd() : ''}

    ${endingBar ? EndingBar() : ''}

    ${barLine ? BarLine() : ''}

    ${ChordName({x:72, y:16, chordName:name})}

    ${max > 6 ? '' : Nut()}


    ${EmptyGrid()}

    ${PositionNumber({
      y: 44 + 16 * (droppedMin - 1), 
      position: droppedMin === 0 ? '' : position})}
    

    ${gridContents}
      
    </svg>
  `;
};

