//Triangle

import { svg } from "lit-element";

//3 4 5
// 6 8 10
const scale = 1.8;
const s1 = 6 * scale;
const s2 = 8 * scale;
export const Triangle = ({ x, y, isBig = false }) => {
  return svg`
    ${
      isBig
        ? svg`
          <polygon 
            points="${x},${y - s2 / 2} ${x + s1},${y - s2 / 2 + s2} ${x - s1},${y - s2 / 2 + s2}" 
            fill="none" stroke='#333333' stroke-width="2">
          </polygon>
        `
        : svg`
          <polygon 
            points="${x},${y - 3} ${x + 6},${y - 3 + 8} ${x - 6},${y - 3 + 8}" 
            fill="none" stroke="#333333" stroke-width="2">
          </polygon>
    `
    }  
`;
};

