import { html, LitElement } from "lit-element";
import page from "page";
import uuid from "uuid";

import "./pages/EditorPage";
import "./pages/SheetsPage";
import { ContactPage } from "./pages/ContactPage";
import { HowToUsePage } from "./pages/HowToUsePage";
import "./pages/SettingsPage";

import "./components/NavBar";
import { ErrorPage } from './pages/ErrorPage.js';

// import { loaded } from 'tone';


class App extends LitElement {
  createRenderRoot() {
    return this;
  }

  static get properties() {
    return {
      page: { type: String },
      sheetId: { type: String },
      isAlternateOrder: { type: Boolean },
      // sheets: { type: Array },
      chordCache: { attribute: false },
      soundsEnabled: { attribute: false }
    };
  }


  constructor() {
    super();

    this.page = "editor";
    this.sheetId = uuid.v4();
    this.soundsEnabled = true;

    const chordCache = JSON.parse(window.localStorage.getItem('chord-cache')) || [];
    // console.log(chordCache);
    this._chordCache = new Set(chordCache);
    // console.log(this._chordCache);
    // Add Some Chords
    // this._chordCache.add("3.x.3.3.3.x@Gm7");
    // this._chordCache.add('x.3.2.3.3.x@C9');
    // this._chordCache.add('1.x.2.2.1.x@Fmaj7');
    // this._chordCache.add('5.x.5.5.5.x@Am7');
    

    this.chordCache = Array.from(this._chordCache);


    this.addEventListener('add-chord', (ev) => {
      // console.log("App", ev.detail);
      this._chordCache.add(ev.detail);
      // console.log(this._chordCache);
      this.chordCache = Array.from(this._chordCache);
      // console.log(this.chordCache);
    });

    this.addEventListener('remove-cached-chord', (ev) => {
      // console.log("App", ev.detail);
      // console.log(this._chordCache.has(ev.detail));
      if(this._chordCache.has(ev.detail)){
        this._chordCache.delete(ev.detail);
        this.chordCache = Array.from(this._chordCache);
      }
    });

    this.addEventListener('import-chords', (ev) => {
      // console.log(ev.detail);
      const chords = ev.detail;
      chords.forEach(chord => {
        this._chordCache.add(chord);
        this.chordCache = Array.from(this._chordCache);
      });
    });


    this.addEventListener('toggle-sound', () => {
      // console.log('toggle-sounds');
      this.soundsEnabled = !this.soundsEnabled;
    })

    window.addEventListener('beforeunload', () => {
      // console.log(this._chordCache);
      window.localStorage.setItem('chord-cache', JSON.stringify([...this._chordCache]))
    })

    this.isAlternateOrder = JSON.parse(window.localStorage.getItem('is-alternate-order')) || false;
    // console.log('this.isAlternateOrder', this.isAlternateOrder);

    page.redirect("/", "/editor/" + this.sheetId);
    // page.redirect('/editor', )
    // page('/editor', this._editorRoute.bind(this));
    page("/editor/:sheetId", this._editorRoute.bind(this));
    page("/sheets", this._sheetsRoute.bind(this));
    page("/contact", this._contactRoute.bind(this));
    page("/how-to-use", this._howToUseRoute.bind(this));
    page("/settings", this._settingsRoute.bind(this));
    page("*", this._notFoundRoute.bind(this));
    page();
  }

  connectedCallback() {
    super.connectedCallback();
  }



  // firstUpdated(){
  //   console.log('App:first updated');
  //   loaded().then(() => {
  //     console.log('loaded')
  //   }).catch(er => {
  //     console.log(er);
  //   })
    
  // }

  _notFoundRoute() {
    this.page = "error-page";
  }

  _editorRoute(ctx) {
    // console.error("_editor route", ctx);
    // console.log('this.sheetId', this.sheetId);
    // console.log('params.sheetId', ctx.params.sheetId);
    if (ctx.params.sheetId) {
      this.sheetId = ctx.params.sheetId;
    }

    this.page = "editor-page";

    // this.sheetId = ctx.params.sheetId ? ctx.params.sheetId : uuid.v4();
    // console.log("editor", this.sheetId);
  }

  _sheetsRoute() {
    this.page = "sheets-page";
  }

  _contactRoute() {
    this.page = "contact-page";
    // console.log("contact");
  }

  _howToUseRoute() {
    this.page = "how-to-use-page";
    // console.log("how to use");
  }

  _settingsRoute(){
    this.page = "settings-page";
  }


  _renderCurrentView() {
    switch (this.page) {
      case "editor-page":
        return html`<editor-page
          .isAlternateOrder=${this.isAlternateOrder}
          .sheetId=${this.sheetId}
          .chordCache=${this.chordCache}
          .soundsEnabled=${this.soundsEnabled}
        ></editor-page>`;
      case "sheets-page":
        return html`<sheets-page></sheets-page>`;
      case "how-to-use-page":
        return HowToUsePage();
      case "contact-page":
        return ContactPage();
      case "settings-page":
        return html`<settings-page .isAlternateOrder=${this.isAlternateOrder} @ordering-changed=${this.handleOrderingChange}></settings-page>`;
      case "error-page":
        return ErrorPage();
        default:
          return html`<editor-page
          .sheetId=${this.sheetId}
        ></editor-page>`;;
    }
  }

  handleOrderingChange(ev){
    // console.log(ev.detail);
    this.isAlternateOrder = ev.detail;
    window.localStorage.setItem('is-alternate-order', JSON.stringify(ev.detail));
  }

  render() {
    // console.log('App', this.sheetId);
    return html`
      <app-navbar .sheetId=${this.sheetId} .page=${this.page}></app-navbar>
      <div>${this._renderCurrentView()}</div>
    `;
  }
}

