//ChordGridInput.js
import { LitElement, html } from 'lit-element';
import { classMap } from 'lit-html/directives/class-map';
import { ChordGrid } from '../chordGrid/ChordGrid';
import { getNote, playNote, playSymbols } from '../../sounds/player';

class ChordGridInput extends LitElement {

  createRenderRoot(){ return this; }

  constructor(){
    super();
    this.chordString = "3.x.3.3.3.5@Gm9";
    this.highlight = false;
  }

  static get properties(){
    return {
      chordString: { type: String },
      highlight: { type: Boolean },
      isAlternateOrder: { type: Boolean },
      soundsEnabled: { attribute: false },
    }
  }


  handleChange(ev, stringIndex){
    // const value = ev.target.value;
    // console.log({stringIndex, value});
    if(ev.target.value && stringIndex !== -1){
      const values = ev.target.value.split('/');
      const fretStringValue = values[values.length - 1];
      const fretIndex = parseInt(fretStringValue);
      if(fretIndex >= 0 && this.soundsEnabled){
        const note = getNote(stringIndex, fretIndex);
        playNote(note);
      }
    }
  
    const E = this.inputs[5].value;
    const A = this.inputs[4].value;
    const D = this.inputs[3].value;
    const G = this.inputs[2].value;
    const B = this.inputs[1].value;
    const e = this.inputs[0].value;
    const name = this.inputs[6].value;
    const frets = [E, A, D, G, B, e].join('.');
    const chord = [frets, this.formatChordName(name)].join('@');
    // console.log('change', chord);
    this.chordString = chord;

    // this.props.onUpdateChord(chord);

  }

  firstUpdated(){
    super.firstUpdated();
    this.inputs = this.querySelectorAll('input[type=text]');
    this.inputs[6].focus();
    // console.log(this.inputs)
  }


  handleFocus(ev, stringIndex){
    // console.log(stringIndex)
    // console.log(stringIndex, ev.target.value)
    if(ev.target.value && stringIndex !== -1){
      // stopNotes();
      const values = ev.target.value.split('/');
      // console.log(values, values[values.length - 1]);
      const fretStringValue = values[values.length - 1]
      // const fretIndex = parseInt(ev.target.value);
      const fretIndex = parseInt(fretStringValue);
      // console.log(fretIndex)
      if(fretIndex >= 0 && this.soundsEnabled){
        const note = getNote(stringIndex, fretIndex);
        playNote(note);
      }
    }
  }

  handleSubmit(ev){
    ev.preventDefault();
    const E = this.inputs[5].value;
    const A = this.inputs[4].value;
    const D = this.inputs[3].value;
    const G = this.inputs[2].value;
    const B = this.inputs[1].value;
    const e = this.inputs[0].value;
    const name = this.inputs[6].value;
    const frets = [E, A, D, G, B, e].join('.');
    // const chord = [frets, name].join('@');
    const chord = [frets, this.formatChordName(name)].join('@');

    // console.log(chord);
    // console.log('chord', chord);
    // this.props.onAddChord(chord);
    this.dispatchEvent(new CustomEvent('add-chord', {detail: chord, bubbles: true}))
    if (window.innerWidth >= 768) this.inputs[6].focus();

    if(this.soundsEnabled){
      playSymbols(chord)
    }
  }

  formatChordName(chordName) {
    const match = chordName.match(/^[a-gA-G#b]{1,2}/);
    if (!match) return;
    const chordRoot = match[0];
    const chordQuality = chordName.replace(/^[a-gA-G#b]{1,2}/, '');
    //console.log(chordRoot);
    return chordRoot.length === 1 ? chordRoot.toUpperCase() + chordQuality :
      chordRoot.substr(0, 1).toUpperCase().concat(chordRoot.substr(1, 1)) + chordQuality

  }

  render(){
    // console.log('GridInput render', this.chordString)
    const [frets, name] = this.chordString.split('@');
    const [E, A, D, G, B, e] = frets.split('.');
    // console.log(frets, name);
    return html`
      <div class='text-center'>
        <div class="py-4 ${classMap({'chord-selected': this.highlight})}">
          ${ChordGrid({chordString: this.chordString, isAlternateOrder: this.isAlternateOrder})}
        </div>
        <div>
          <form @submit=${this.handleSubmit}
            class='gridInput mt-2'>
            <div class="border border-light">
              <input type="text" class="string-input"  .value=${e} tabindex='7' 
                @focus=${(e) => this.handleFocus(e,0)}
                @input=${(e) => this.handleChange(e, 0)} />
              <input type="text"  class="string-input"  .value=${B} tabindex='6' 
                @focus=${(e) => this.handleFocus(e,1)} 
                @input=${(e) => this.handleChange(e, 1)} />
              <input type="text" class="string-input"  .value=${G} tabindex='5' 
                @focus=${(e) => this.handleFocus(e,2)} 
                @input=${(e) => this.handleChange(e, 2)} />
              <input type="text" class="string-input" .value=${D} tabindex='4' 
                @focus=${(e) => this.handleFocus(e,3)} 
                @input=${(e) => this.handleChange(e, 3)} />
              <input type="text" class="string-input"  .value=${A} tabindex='3' 
                @focus=${(e) => this.handleFocus(e,4)} 
                @input=${(e) => this.handleChange(e, 4)} />
              <input type="text" class="string-input"  .value=${E} tabindex='2' 
                @focus=${(e) => this.handleFocus(e,5)} 
                @input=${(e) => this.handleChange(e, 5)} />
            </div>

            <input autocomplete="off" type="text" id="chord-name-input" style="width: 100%" 
              class='text-center mt-2 form-control' .value=${name} tabindex="1" 
              
              @input=${(e) => this.handleChange(e, -1)} />


            <button class='form-control btn ripple btn-primary btn-sm' tabindex='8'>Add Chord</button>


          </form>

        </div>
      </div>
    `
  }
}

customElements.define('chord-grid-input', ChordGridInput);