import { LitElement, html } from "lit-element";
import uuid from "uuid";
import page from 'page';
import "../components/SheetEditor";
import {
  CreateNewSheetIcon,
  EditIcon,
  PreviewIcon,
  SaveIcon,
  SaveAsNewIcon,
  DownLoadIcon,
} from "../components/Icons.js";
import { Preview } from "../components/Preview.js";
import { creatHTMLContent } from '../helpers/createHTMLContent.js';

const saveInterval = 1000 * 60 * 2;

class EditorPage extends LitElement {
  createRenderRoot() {
    return this;
  }

  static get properties() {
    return {
      sheetId: { attribute: false },
      isPreviewing: { attribute: false },
      songInfo: { attribute: false },
      chords: { attribute: false },
      isSaved: { attribute: false },
      isAlternateOrder: { attribute: false },
      chordCache: { attribute: false },
      soundsEnabled: { attribute: false },
    };
  }

  constructor() {
    super();
    // this.sheetId = uuid.v4();
    this.isPreviewing = false;
    this.songInfo = {
      name: "",
      description: "",
      leftSubtitle: "",
      rightSubtitle: ""
    };
    this.chords = [];

    this.isSaved = true;

  }

  connectedCallback(){
    super.connectedCallback();
    // console.warn(this.sheetId);
    this.autoSaveInterval = window.setInterval(() => {
      // console.log('auto save');
      if(this.chords.length > 0){
        this.handleSave();
      }
    }, saveInterval)
  }

  disconnectedCallback(){
    window.clearInterval(this.autoSaveInterval);
  }

  firstUpdated() {
    // console.log("Editor PAge first update", this.sheetId);
    const currentSheet = JSON.parse(localStorage.getItem(this.sheetId))
    // console.log(currentSheet);
    if (currentSheet) {
      this.chords = currentSheet.chords;
      this.songInfo = currentSheet.songInfo;
    }
  }

  // updated(changedProps){
  //   console.log(changedProps)
  //   if(changedProps.has('chords') || changedProps.has('songInfo')){
  //     console.log('chords or songInfo changed');
  //     // this.isSaved = false;
  //   }
  // }
  
  handleSetChords(ev){
    // console.log(ev.detail);
    this.chords = ev.detail;
    this.isSaved = false;
  }

  handleAddSongInfo(ev){
    this.songInfo = { ...this.songInfo, ...ev.detail }
    this.isSaved = false;
  }

  handlePreview() {
    this.isPreviewing = !this.isPreviewing;
    this.handleSave();
  }

  handleSave(){
    window.localStorage.setItem(
      this.sheetId,
      JSON.stringify({
        sheetId: this.sheetId,
        chords: this.chords,
        songInfo: this.songInfo,
        date: Date.now()
      })
    );    
    this.isSaved = true;
  }

  handleSaveAsNew() {

    if(this.chords.length > 0){
      const saveCurrentSheet = window.confirm('Save changes in current sheet?');
      // console.log(saveCurrentSheet);
      if(saveCurrentSheet){
        this.handleSave();
      }
    }
    
    
    const newId = uuid.v4();
    // this.setState({ sheetId: newId });
    window.localStorage.setItem(
      newId,
      JSON.stringify({
        sheetId: newId,
        chords: this.chords,
        songInfo: this.songInfo,
        date: Date.now()
      })
    );

    page(`/editor/${newId}`);


    // this.props.history.push({
    //   pathname: "/" + newId,
    // });
  }

  handleNewSheet() {
    if(this.chords.length > 0){
      const saveCurrentSheet = window.confirm('Save changes in current sheet?');
      // console.log(saveCurrentSheet);
      if(saveCurrentSheet){
        this.handleSave();
      }
    }
    const newId = uuid.v4();
    page(`/editor/${newId}`);
    this.chords = [];
    this.songInfo = {name: '', description: ''}
  }

  handleDownload() {
    const element = window.document.getElementById("page");
    const html = creatHTMLContent(element.innerHTML);
    const dataURI = "data:text/html," + encodeURIComponent(html);
    //console.log(dataURI)
    //window.document.location = dataURI;
    const a = window.document.createElement("a");
    a.href = dataURI;
    a.download = this.songInfo.name + ".html";
    window.document.body.appendChild(a);
    a.click();
  }

  handlePrintPreview() {
    const tab = window.open();
    const element = window.document.getElementById("page");
    const html = creatHTMLContent(element.innerHTML);
    // console.log(html);
    tab.document.write(html);
    tab.document.close();
    tab.document.title = this.songInfo.name;
    // window.print();
  }



  render() {
    // console.warn("Editor Page:render", this.sheetId, this.chords);
    const previewButtonText = this.isPreviewing
      ? html`${EditIcon()}`
      : html`${PreviewIcon()}`;
    return html`
      <div class="fadeIn">
        <div class="text-center shadow-sm">
          <div class="btn-group mt-2 mb-2" role="group">
            <button
              class="btn btn-primary btn ripple"
              title=${this.isPreviewing ? "Edit" : "Preview"}
              @click=${this.handlePreview}
            >
              ${previewButtonText}
            </button>
            ${!this.isPreviewing
              ? html`
                  <button
                    title="Save sheet"
                    class="btn btn-primary btn"
                    @click=${this.handleSave}
                  >
                    ${SaveIcon()} ${this.isSaved ? '' : '*'}
                  </button>
                  <button
                    title="Create new sheet"
                    class="btn btn-primary btn ripple d-none d-sm-block"
                    @click=${this.handleNewSheet}
                  >
                    ${CreateNewSheetIcon()}
                  </button>
                  <button
                    title="Transfer to new sheet"
                    class="btn btn-primary btn ripple d-none d-sm-block"
                    @click=${this.handleSaveAsNew}
                  >
                    ${SaveAsNewIcon()}
                  </button> 
                `
              : html`
                  <button
                    title="Download"
                    class="btn btn-primary btn ripple"
                    @click=${this.handleDownload}
                  >
                    ${DownLoadIcon()}
                  </button>
                  <button
                    title="Print preview"
                    class="btn btn-primary btn ripple"
                    @click=${this.handlePrintPreview}
                  >
                    ${PreviewIcon()}
                  </button>
                `}
          </div>
        </div>
        <!-- ${this.sheetId} -->
        ${this.isPreviewing
          ? Preview({ chords: this.chords, songInfo: this.songInfo, isAlternateOrder: this.isAlternateOrder })
          : html`<sheet-editor
            .soundsEnabled=${this.soundsEnabled}
            .chordCache=${this.chordCache}
            .isAlternateOrder=${this.isAlternateOrder}
            .songInfo=${this.songInfo}
            @set-song-info=${this.handleAddSongInfo}
            .chords=${this.chords}
            @set-chords=${this.handleSetChords}
            ></sheet-editor>`}
      </div>
    `;
  }
}
