export const MarkX = ({ x, y, isLight=false, hasTriangle=false }) => {
  const color = isLight ? '#ffffff' : '#333333';
  return svg`
  ${hasTriangle ? svg`<rect x=${x - 4} y=${y - 4} width="8" height="12" fill="#fff"></rect>
` : ''}
  <text x=${x} y=${y + (hasTriangle ? 3 : 2)}
    fill=${color} stroke=${isLight ? 'none' : '#333333'} font-family="sans-serif" 
    text-anchor="middle" font-size="14px">
    <tspan dy="3.1171875">${hasTriangle ? 'x' : 'X'}</tspan>
  </text>
`;
}

