export const Preview = ({chords, songInfo, isAlternateOrder}) => {
  return html`
    <div id='page' class='container fadeIn'>  
      <h3 style="margin-top:1em;text-align:center;"><em>${songInfo.name}</em></h3>
      <div style="display:flex;">
        <div style="opacity:0.6;width:33.3%;text-align:left;font-size:0.8rem;">${songInfo.leftSubtitle}</div>
        <div style="opacity:0.6;width:33.3%;text-align:center;font-size:0.8rem;">${songInfo.description}</div>
        <div style="opacity:0.6;width:33.3%;text-align:right;font-size:0.8rem;">${songInfo.rightSubtitle}</div>
      </div>
      <div style="display:flex;flex-wrap:wrap;align-items:center;"> 
        ${chords.map(chord => {
          return html`
            <div style="margin-top:0.5rem;padding-top:0.5rem;padding-bottom:0.5rem;">
              <div style="height: 1.2em;">
                ${chord.section ? SectionName(chord.section) : ''}    
              </div>
              <div style="margin-top: 0.5rem;">
                ${ChordGrid({...chord, isAlternateOrder})}
              </div>
            </div>
          `
        })}
      </div>
    </div>
  `;

}