import { LitElement, html } from "lit-element";
import { DeleteForeverIcon, EditIcon, PublishIcon } from "../components/Icons";
import page from 'page';
import { options } from "../helpers/saveFileOptions";
import { classMap } from 'lit-html/directives/class-map';
import { styleMap } from 'lit-html/directives/style-map';
import { isImportExportSupported } from '../helpers/isImportExportSupported';

class SheetsPage extends LitElement {
  createRenderRoot() {
    return this;
  }

  constructor(){
    super();
    const sheets = Object.keys(localStorage)
      .map((it) => JSON.parse(localStorage.getItem(it)))
      .filter((it) => it.sheetId);

    const copy = sheets.slice(0);
    const sorted = copy.sort((a,b) => b.date - a.date);
    // console.log(sheets);
    this.sheets = sorted;
    this.sortAscending = false;

    this.sortAZ = true;
  }

  static get properties(){
    return {
      sheets: { type: Array }
    }
  }



  handleDeleteSheet(sheetId){
    window.localStorage.removeItem(sheetId);
    this.sheets = this.sheets.filter( sheet => sheet.sheetId !== sheetId );
  }

  async handleExportSheet(sheetId){
    const sheet = JSON.parse(window.localStorage.getItem(sheetId));
    // console.log(sheet);

    try {
      const handle = await window.showSaveFilePicker(options);
      const writable = await handle.createWritable();
      await writable.write(JSON.stringify([sheet]));
      await writable.close();
    } catch (error) {
      // console.log(error.message);
      window.alert("Sorry export failed...");
    }
    
  }

  handleClick(item){
    // console.log(item)
    page(`/editor/${item.sheetId}`);
  }

  handleSortSheetsByDate = () => {
    if(this.sortAscending){
      const copy = this.sheets.slice(0);
      const sorted = copy.sort((a,b) => b.date - a.date);
      this.sheets = sorted;
      this.sortAscending = false;
    } else {
      const copy = this.sheets.slice(0);
      const sorted = copy.sort((a,b) => a.date - b.date);
      this.sheets = sorted;
      this.sortAscending = true;
    }
  }

  handleSortSheetsByName = () => {
    if(this.sortAZ){
      const copy = this.sheets.slice(0);
      const sorted = copy.sort((a,b) => {
      const x = a.songInfo.name.toLowerCase();
      const y = b.songInfo.name.toLowerCase();
      return x < y ? -1 : x > y ? 1 : 0;
      });
      this.sheets = sorted;
      this.sortAZ = false;
    } else {
      const copy = this.sheets.slice(0);
      const sorted = copy.sort((a,b) => {
      const x = a.songInfo.name.toLowerCase();
      const y = b.songInfo.name.toLowerCase();
      return x > y ? -1 : x < y ? 1 : 0;
      });
      this.sheets = sorted;
      this.sortAZ = true;
    }
    
  }

  render() {
    return html`
      <div class="container my-3 fadeIn">
        <p>
          ${this.sheets.length === 0 ? 'Your sheets will appear here as you save them.' : ''}
        </p>
        <div class="w-100 d-flex align-items-center justify-content-center ${classMap({'invisible': this.sheets.length === 0})}">
          <div class="btn-group mb-2">
            <button class="btn btn-primary" @click=${this.handleSortSheetsByDate}>Sort by Date</button>
            <button class="btn btn-primary" @click=${this.handleSortSheetsByName}>Sort by Name</button>
          </div>
        </div>
        <div class="d-flex flex-column alternate">
          ${this.sheets.map(sheet => {
            return html`
              <div class="d-flex flex-row align-items-center p-2 border-bottom">
                <div class="w-25">${new Date(sheet.date).toDateString()}</div>
                <div class="w-50">
                  ${sheet.songInfo.name}
                  ${sheet.songInfo.description}
                </div>
                <div class="w-25 d-flex justify-content-around">
                  <button
                    title="Edit sheet"
                    class="btn btn-primary btn-sm"
                    @click=${() => this.handleClick(sheet)}
                  > ${EditIcon()}
                  </button>
                  <button
                    style=${styleMap({display: isImportExportSupported ? 'block' : 'none'})}
                    title="Export sheet data"
                    class="btn btn-primary btn-sm"
                    @click=${() => this.handleExportSheet(sheet.sheetId)}
                  >
                    ${PublishIcon()}
                  </button>
                  <button
                    title="Delete sheet"
                    class="btn btn-warning btn-sm"
                    @click=${() => this.handleDeleteSheet(sheet.sheetId)}
                  >
                    ${DeleteForeverIcon()}
                  </button>
                </div>
              </div>
            `

          })}
        </div>
      </div>
    `;
  }
}

customElements.define('sheets-page', SheetsPage);