import { LitElement, html } from 'lit-element';
import { styleMap } from 'lit-html/directives/style-map';
import { classMap } from 'lit-html/directives/class-map';

class NavBar extends LitElement {
  createRenderRoot(){ return this; }
  static get properties(){
    return {
      showCollapse: { attribute: false },
      sheetId: { attribute: false },
      page: { attribute: false },
    }
  }
  constructor(){
    super();
    this.showCollapse = false;
  }

  render(){
    return html`
      <nav class="navbar navbar-expand-sm navbar-dark bg-primary">
      <span class="navbar-brand mb-0 h1">Timothy</span>
      <button
        @click=${() => this.showCollapse = !this.showCollapse}
        class="navbar-toggler"
        type="button"
        data-toggle="collapse"
        data-target="#navbarNav"
        aria-controls="navbarNav"
        aria-expanded="false"
        aria-label="Toggle navigation"
      >
        <span class="navbar-toggler-icon"></span>
      </button>
      <div
        class="justify-content-between collapse navbar-collapse"
        id="navbarNav"
        style=${styleMap({display: this.showCollapse ? 'block' : 'none'})}
      >
        <ul class="navbar-nav flex-grow-1">
          <li class="nav-item" >
            <a href="/editor/${this.sheetId}" 
              class="nav-link ${classMap({active: this.page === 'editor-page'})}">Editor</a>
          </li>
          <li class="nav-item">
            <a href="/sheets" 
              class="nav-link ${classMap({active: this.page === 'sheets-page'})}">Sheets</a>
          </li>
          <li class="nav-item">
            <a href="/how-to-use" 
              class="nav-link ${classMap({active: this.page === 'how-to-use-page'})}">How To</a>
          </li>
          <li class="nav-item">
            <a href="/settings" 
              class="nav-link ${classMap({active: this.page === 'settings-page'})}">Settings</a>
          </li>
          <li class="nav-item">
            <a href="/contact" 
              class="nav-link ${classMap({active: this.page === 'contact-page'})}">Contact</a>
          </li>
        </ul>
      </div>
    </nav>
    `;
  }


}

customElements.define('app-navbar', NavBar);

