export const chordStringToFrets = (chordString) => {
  // console.log(chordString);
  const [frets] = chordString.split('@');
  const fretsArray = frets.split('.').map(it => {
    const fs = it.split('/');
    // console.log(fs)
    return [
      !isNaN(parseInt(fs[0])) ? fs[0] : -1,
      !isNaN(parseInt(fs[1])) ? fs[1] : -1,
      !isNaN(parseInt(fs[2])) ? fs[2] : -1,
      !isNaN(parseInt(fs[3])) ? fs[3] : -1,
  ]});
  // console.log(fretsArray)
  //const transformed = [0,1,2,3].map(i => fretsArray[][i]);
  const transformed = fretsArray[0].map((_, colIndex) => fretsArray.map(row => row[colIndex]));
  // console.log(transformed);

  const fretsStrings = transformed.map(it => 
    it.map((i, index) => i === -1 ? -1 : [5 - index, i])
    .filter(it => it !== -1)
  ).filter(it => it.length !== 0);
  // console.log({fretsStrings});
  return fretsStrings;
  // return fretsArray
  //   .map(it => parseInt(it, 10))
  //   .map((it, index) => isNaN(it) ? -1 : [5 - index, it])
  //   .filter(it => it !== -1);
}

