export const getChordNotes = function(chordString) {
  // const stringsFrets = getStringNumberAndFrets(template);
  const stringsFrets = chordStringToFrets(chordString);
  console.log({stringsFrets})
  return stringsFrets.map(it => it.map(it => getNote(it[0], it[1])))
  .filter(it => it.length !== 0);
}

