let part = null;
export const playSymbols = (chordString) => {
  if(context.state === 'suspended') { start(); }
  if(!jazzGuitar.loaded){return;}
  // console.log(Transport.state)
  if(Transport.state === "started"){
    Transport.stop();
    if(part){
      part.dispose();
    }
  }
  const stringsToFrets = chordStringToFrets(chordString);
  // console.log({stringsToFrets})
  const notesToPlay = stringsToFrets.map(it => it.map(it => getNote(it[0], it[1])))
  .filter(it => it.length !== 0);
  // console.log({notesToPlay});
  const events = notesToPlay.map((it,index) => ({note: it, time: (`+${index * 1}` ), dur: '2n' }))
  // console.log(events)
  part = new Part((time, event) => {
    jazzGuitar.triggerAttackRelease(event.note, event.dur, time);
  }, events);
  part.start(0);
  Transport.start();

}


