import { html, LitElement } from "lit-element";
import { ChordGrid } from "../components/chordGrid/ChordGrid.js";
import {
  DotIcon,
  MarkXIcon,
  RectangleIcon,
  TriangleIcon
} from "../components/Icons.js";

import { isImportExportSupported } from '../helpers/isImportExportSupported';

import { options } from '../helpers/saveFileOptions';

class SettingsPage extends LitElement {
  createRenderRoot() {
    return this;
  }

  constructor() {
    super();
    this.isExperimental = false;
    this.importState = '';
  }

  static get properties() {
    return {
      isAlternateOrder: { type: Boolean },
      isExperimental: { type: Boolean },
      importState: { attribute: false }
    };
  }

  handleToggle(ev) {
    this.dispatchEvent(
      new CustomEvent("ordering-changed", { detail: ev.target.checked })
    );
  }

  handleEnableExperimental(ev){
    this.isExperimental = ev.target.checked;
  }

  getAllSheets() {
    const sheets = Object.keys(window.localStorage)
      .map((it) => JSON.parse(window.localStorage.getItem(it)))
      .filter((it) => it.sheetId);

    return sheets;
  }

  async handleSheetsExport() {
    
    this.importState = "Exporting sheets..."

    try {

      const sheets = this.getAllSheets();
      if(sheets.length === 0){
        throw new Error('There are no sheets to export yet.');
      }
      const handle = await window.showSaveFilePicker(options);
      const writable = await handle.createWritable();
      await writable.write(JSON.stringify(sheets));
      await writable.close();
      this.importState = "Export complete."
    } catch (error) {
      this.importState = 'Sorry export failed.';
    }

    window.setTimeout(() => this.importState = '', 3000);

  }

  async handleSheetsImport() {
    this.importState = "Importing sheets...";

    try {
      const [fileHandle] = await window.showOpenFilePicker();
      const file = await fileHandle.getFile();
      const contents = await file.text();
      const sheetsData = JSON.parse(contents);
      
      sheetsData.forEach((sheet) => {
        if (sheet.sheetId && Array.isArray(sheet.chords)) {
          window.localStorage.setItem(sheet.sheetId, JSON.stringify(sheet));
        } else {
          throw new Error('Invalid sheets data...');
        }
      });

      this.importState = 'Import complete.';

    } catch (error) {
      this.importState = 'Import failed.';
    }

    window.setTimeout(() => this.importState = '', 3000);

  }

  async handleChordsExport() {

    this.importState = "Exporting chords...";

    try {
      const chords = JSON.parse(window.localStorage.getItem("chord-cache")) || [];
      if (chords.length === 0) {
        throw new Error('There are no chords in cache.');
        // return;
      }
      const handle = await window.showSaveFilePicker(options);
      const writable = await handle.createWritable();
      await writable.write(JSON.stringify(chords));
      await writable.close();
      this.importState = "Export complete.";

    } catch (error) {
      this.importState = 'Sorry export failed.';
    }

    window.setTimeout(() => this.importState = '', 3000);

  }

  async handleChordsImport() {
    this.importState = "Importing chords...";
    const validChords = [];

    try {
      const [fileHandle] = await window.showOpenFilePicker();
      const file = await fileHandle.getFile();
      const contents = await file.text();
      const chordsData = JSON.parse(contents);
      chordsData.forEach(chord => {
        if(typeof chord !== 'string') { 
          throw new Error('Invalid chords data.')
        }
        if(!/@/.test(chord)) {
          throw new Error('Invalid chords data.')
        }

        const [frets] = chord.split('@');
        const fretsArray = frets.split('.')
     
        if(!frets || fretsArray.length !== 6){
          throw new Error('Invalid chords data.')
        }
        if(/[a-gA-G#b]{1,2}/.test(chord)){
          validChords.push(chord);
        }
      });

      if(validChords.length > 0){
        this.dispatchEvent(
          new CustomEvent("import-chords", { detail: validChords, bubbles: true })
        );
      }

      this.importState = 'Import complete.'

    } catch (error) {
      this.importState = 'Import failed.';
    }

    window.setTimeout(() => this.importState = '', 3000);
  }


  render() {
    // console.log('settings page render')
    return html` <div class="container mt-4 fadeIn">
      <!-- switch -->
      <div
        style="display:flex;justify-content:space-between;align-items:center;"
        class="mt-4"
      >
        <div>
          <label for="alternate-symbol-order">Use Alternate Symbol Order</label>
        </div>
        <div>
          <label class="switch">
            <input
              id="alternate-symbol-order"
              @change=${this.handleToggle}
              type="checkbox"
              ?checked=${this.isAlternateOrder}
            />
            <span class="slider round"></span>
          </label>
        </div>
      </div>
      <!-- end switch -->

      <div class="d-flex aling-items-center justify-content-center mt-4">
        ${ChordGrid({
          chordString: "3.x.3.3.3/5/6.x/x/x/5@Gm7",
          isAlternateOrder: this.isAlternateOrder,
        })}
        ${ChordGrid({
          chordString: "8.x.8.9.x/x/x/11.11/9/8@C7#5#9",
          isAlternateOrder: this.isAlternateOrder,
        })}
        ${ChordGrid({
          chordString: "x.8.11.9.10.x@Fmaj7#5",
          isAlternateOrder: this.isAlternateOrder,
        })}
      </div>
      <div class="text-center mt-4">
        Playing order is:
        ${this.isAlternateOrder
          ? html`${DotIcon()} ${MarkXIcon()} ${TriangleIcon()}
            ${RectangleIcon()}`
          : html`${DotIcon()} ${MarkXIcon()} ${RectangleIcon()}
            ${TriangleIcon()}`}
      </div>

      ${isImportExportSupported
        ? html`
            <!-- switch -->
            <div
              style="display:flex;justify-content:space-between;align-items:center;"
              class="mt-4"
            >
              <div>
                <label for="enable-experimental-features"
                  >Enable Experimental Features</label
                >
              </div>
              <div>
                <label class="switch">
                  <input
                    id="enable-experimental-features"
                    @change=${this.handleEnableExperimental}
                    type="checkbox"
                    ?checked=${this.isExperimental}
                  />
                  <span class="slider round"></span>
                </label>
              </div>
            </div>
            <!-- end switch -->
          `
        : ""}
      ${isImportExportSupported && this.isExperimental
        ? html`
          <div class="fadeIn text-center d-flex flex-column align-items-center">
            <div class="btn-group mt-4 w-50">
              <button @click=${this.handleSheetsExport} class="btn btn-primary">
                Export Sheets Data
              </button>
              <button @click=${this.handleSheetsImport} class="btn btn-primary">
                Import Sheet(s) Data
              </button>
            </div>
            <div class="btn-group mt-4 w-50">
              <button @click=${this.handleChordsExport} class="btn btn-primary">
                Export Chords Cache
              </button>
              <button @click=${this.handleChordsImport} class="btn btn-primary">
                Import Chords Cache
              </button>
            </div>
            <div class="mt-4">
              ${this.importState}
            </div>
          </div>
            
          `
        : ""}
    </div>`;
  }
}

customElements.define("settings-page", SettingsPage);
