import { svg } from 'lit-element';

// const width = 15;
export const Rectangle = ({ x, y, isBig=false }) => { 
  const width = isBig ? 15 : 10;
  return svg`
  <rect
    x=${x-(width/2)} y=${y-(width/2)} 
    width=${width} height=${width} 
    stroke-width="2"
    fill="none" stroke="#333333">
  </rect>
`;
}